$(document).ready(function() {

    sliderServices();
    sliderSpecialists();
    sliderInfo();
    sliderDiagnoses();

    // Burger - Menu
    $('.header-top__burger').click(function () {
        $(this).toggleClass('header-top__burger--active')
        $('.header-top__left-wrapp').toggleClass('header-top__left-wrapp--active')
        $('.header__menu-bg').toggleClass('header__menu-bg--active')
        $('body').toggleClass('fixed-body')
    })

    // Menu - Link
    $('.header-bottom__item').hover(function () {
        $(this).toggleClass('header-bottom__item--active')
    })

    // Accordion
    $(".header-top__item-show").click((function(){
        $(this).toggleClass("header-top__item-show--active"),
        $(this).siblings(".header-top__item-hidden").slideToggle()}));


    if (window.matchMedia("(max-width: 768px)").matches) {
        $('.footer-top__item-show').click(function () {
            $(this).toggleClass('footer-top__item-show--active')
            $(this).siblings(".footer-top__item-hidden").slideToggle();
        })
    }

    // Up
    var btn = $('.up');

    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });

    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

})
